    opacity: 1;
  }
}
.c-photo {
  position: relative;
  width: 100%;
  height: 100%;
}
.u-mask {
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0,0,0,.6);
  z-index: 1;
}
.g-in {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 2;
  .u-title {
    margin-top: 60px;
    font-size: 42px;
    color: white;
    .icon {
      display: inline-block;
      vertical-align: middle;
