  .m-photo {
    position: relative;
    width: 680px;
    height: 680px;
    margin: 65px 0 100px 0;
    .u-inner, .u-border {
      position: absolute;
      left: 0;
      right: 0;
      bottom: 0;
      top: 0;
      margin: auto;
    }
    .u-border {
      width: 599px;
      height: 600px;
      z-index: 3;
      background-image: url('../../assets/p3-photo-border.png');
    }
    .u-inner {
      width: 680px;
      height: 680px;
      background-color: black;
      overflow: hidden;
    }
  }
  .m-btn-group {
    font-size: 0;
    .u-btn {
      display: inline-block;
      width: 195px;
      line-height: 83px;
      text-align: center;
      background-repeat: no-repeat;
      background-size: 100% auto;
      font-size: 31px;
      color: white;
      transition: transform .8s;
      &+.u-btn {
        margin-left: 90px;
      }
      &:active {
        transform: scale(.9);
      }
    }
    .u-back {
      background-image: url('../../assets/p3-back-btn.png');
    }
    .u-save {
      background-image: url('../../assets/p3-save-btn.png');
    }
